@font-face {
  font-family: '28 Days Later';
  src: url('28 Days Later.eot');
  src: url('28 Days Later.woff2') format('woff2'),
       url('28 Days Later.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: '28DaysLater';
  src: url('28DaysLater.woff') format('woff'),
       url('28DaysLater.ttf') format('truetype'),
       url('28DaysLater.svg#28DaysLater') format('svg');
  font-weight: normal;
  font-style: normal;
}

